import styled from "styled-components"

const Image = styled.div`
    box-sizing:border-box;
    height: 200px;
    width: 100%;
    border-radius: 10px;
    font-size:25px;
    font-family: 'Roboto', sans-serif;
    background: gray;
    color: white;
    padding: 25px;
`;

export default Image;